/* stylelint-disable rule-empty-line-before */
/* stylelint-disable comment-empty-line-before */
.col-item {
  width: 80px;
  height: 80px;
  background: none;
  border: 1px solid var(--color-border);
  font-size: 28px;
  color: var(--color-text-2);
  /**
    因为父级设置了flex布局,当宽度超出屏幕时,
    子元素宽度不生效,子元素需要设置此属性flex-shrink,0代表不会收缩
    或者使用min-width代替width
    */
  flex-shrink: 0;
  user-select: none;
}
.row-item {
  display: flex;
  flex-wrap: nowrap;
}
.right {
  background-color: #7bed9f;
}
.wrong {
  background-color: #ff6b81;
}
.tag-info {
  display: inline-block;
  padding: 0 5px;
  margin: 0 5px;
  background: #ced6e0;
  border-radius: 3px;
}
body[arco-theme='dark'] .col-item {
  width: 80px;
  height: 80px;
  background: none;
  border: 1px solid var(--color-border);
  font-size: 28px;
  flex-shrink: 0;
  user-select: none;
}
body[arco-theme='dark'] .row-item {
  display: flex;
  flex-wrap: nowrap;
}
body[arco-theme='dark'] .right {
  background-color: #5bb377;
}
body[arco-theme='dark'] .wrong {
  background-color: #cf586a;
}
body[arco-theme='dark'] .tag-info {
  display: inline-block;
  padding: 0 5px;
  margin: 0 5px;
  background: #ced6e0;
  border-radius: 3px;
  color: #333;
}
